@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body::-webkit-scrollbar {
  width: 0.5rem;
}

body::-webkit-scrollbar-track {
  background: black;
}

body::-webkit-scrollbar-thumb {
  background: gray;
  border-radius: 10px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: "Josefin Sans", sans-serif;
}

.content {
  width: 85%;
  margin: 3rem auto;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
}

.content img {
  width: 70%;
  height: 30rem;
  object-fit: cover;
}

.content .info {
  width: 35rem;
  max-width: 70%;
  background-color: rgba(256, 256, 256, 0.7);
  padding: 4rem 0 0 5rem;
  position: absolute;
  bottom: 0;
  right: 0;
}

.content .info h2 {
  font-size: 2.5rem;
  letter-spacing: 2px;
  margin-bottom: 0;
}
.content .info p {
  line-height: 1.5;
  margin-bottom: 0;
}

@media screen and (max-width: 720px) {
  .content {
    width: 90%;
  }
  .content img {
    width: 100%;
    height: auto;
  }
  .content .info {
    position: relative;
    max-width: 100%;
    text-align: center;
    padding: 0;
  }
  .content .info h2 {
    font-size: 1.5rem;
    margin-top: 2rem;
    margin-bottom: 1rem;
  }
  .content .info p {
    width: 85%;
    margin-left: auto;
    margin-right: auto;
  }
}

footer {
  background-color: black;
  color: white;
  padding: 2rem 3%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

footer ul {
  padding: 0;
  list-style: none;
}

footer li:nth-child(1) {
  padding: 0;
}

footer li {
  display: inline-block;
  padding-left: 1rem;
}

.f2 {
  text-align: right;
}

.f2 img {
  margin-left: 1rem;
  margin-bottom: 1rem;
}

@media screen and (max-width: 720px) {
  footer {
    flex-direction: column;
    text-align: center;
    align-items: center;
  }
  footer li {
    padding: 0;
    margin-bottom: 1.3rem;
    display: block;
  }
  .f1 {
    margin-bottom: 1.8rem;
  }
  .f1 h2 {
    margin-bottom: 2rem;
  }
  .f2 {
    text-align: center;
    padding-bottom: 1rem;
  }
}

.g {
  width: 85%;
  margin: auto;
  margin-bottom: 4rem;
}

.g-title {
  margin: 4rem auto 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.g-title h2 {
  font-size: 2.5rem;
  letter-spacing: 2px;
}

.g-title h3 {
  font-size: 1rem;
  letter-spacing: 2px;
  padding: 1rem;
  border: 2px solid black;
}

.grid {
  height: 60rem;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.box {
  height: 100%;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.box h3 {
  position: absolute;
  letter-spacing: 2px;
  bottom: 1rem;
  left: 1rem;
  color: white;
  font-size: 1.5rem;
  line-height: 1.25;
  margin-bottom: 0;
}

@media screen and (max-width: 720px) {
  .g {
    width: 90%;
  }
  .grid {
    height: 75rem;
    grid-template-rows: repeat(8, 1fr);
    grid-template-columns: repeat(1, 1fr);
  }
  .box h3 {
    bottom: 1rem;
    left: 1rem;
  }
}

nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 8rem;
  width: 100%;
  padding: 0 6%;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  z-index: 999;
  background-color: rgba(0, 0, 0, 0.15);
}

nav h2 {
  font-size: 2.2rem;
}

nav ul {
  list-style: none;
  padding: 0;
}

nav ul li {
  padding-left: 2rem;
  display: inline-block;
  font-size: 1.1rem;
}

svg {
  display: none;
}

.menu-title {
  display: none;
}

@media screen and (max-width: 720px) {
  nav {
    height: 6rem;
  }
  nav ul {
    position: absolute;
    height: 100vh;
    width: 100vw;
    background: black;
    color: white;
    top: 0;
    left: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
  }
  nav ul li {
    display: block;
    padding-top: 2rem;
    text-transform: uppercase;
    font-size: 1.5rem;
  }
  svg {
    display: block;
    height: 1.5rem;
    width: 1.5rem;
    fill: white;
  }
  .menu-title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 1rem;
    margin-bottom: 1.5rem;
    text-transform: capitalize;
  }
  .menu-title svg {
    margin-right: 1rem;
    color: white;
  }
}

.hero {
  height: 47rem;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  box-sizing: border-box;
  position: relative;
}

.hero h1 {
  margin-left: 8%;
  font-size: 4rem;
  color: white;
  line-height: 1.4;
  box-sizing: border-box;
  padding: 1.5rem;
  position: relative;
  letter-spacing: 0.2rem;
}

.hero .line {
  position: absolute;
  border: 3px solid white;
  top: 0;
  left: 0;
}

@media screen and (max-width: 720px) {
  .hero {
    height: 35rem;
  }
  .hero h1 {
    font-size: 2rem;
  }
}

